@import url(https://fonts.googleapis.com/css?family=KoHo|Roboto);
.header {
  text-align: center;
  height: 100px; }

.search-page-wrapper {
  background-image: url("/dog-3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: top; }

.search-container {
  width: auto;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: rgba(250, 250, 250, 0.6);
  font-family: "KoHo", sans-serif;
  text-align: center; }

h1 {
  font-family: "KoHo", sans-serif; }

h3 {
  margin: 0;
  font-size: 25px; }

.search-container-sex,
.search-container-size,
.search-container-age,
.search-container-catOk,
.search-container-dogOk {
  padding: 20px;
  font-size: 20px; }
  .search-container-sex input,
  .search-container-size input,
  .search-container-age input,
  .search-container-catOk input,
  .search-container-dogOk input {
    margin-left: 20px; }
  .search-container-sex label,
  .search-container-size label,
  .search-container-age label,
  .search-container-catOk label,
  .search-container-dogOk label {
    margin-left: 10px; }

input[type=checkbox] {
  -ms-transform: scale(2);
  /* IE */
  -moz-transform: scale(2);
  /* FF */
  -webkit-transform: scale(2);
  /* Safari and Chrome */
  -o-transform: scale(2);
  /* Opera */
  padding: 10px; }

.submit-button {
  margin: auto;
  position: relative;
  text-align: center; }

.submit-button input {
  padding: 12px 50px;
  background-color: #ffeed6;
  font-size: 15px;
  cursor: pointer; }

.submit-button img {
  width: 32px;
  position: absolute;
  top: 10%;
  left: 30%; }

@media (min-width: 900px) {
  .search-container {
    width: 40%;
    height: 40%;
    margin: 0 auto;
    border-radius: 20px; }
  .submit-button {
    width: 50px; }
  .result-page-wrapper {
    width: 70%;
    padding: 0px 10px; }
  .result-page-wrapper img {
    width: 30%; } }

@media (min-width: 600px) and (max-width: 1200px) {
  .submit-button {
    width: 50px; }
  .search-container {
    width: 60%;
    height: auto;
    margin: 0 auto;
    border-radius: 20px; } }

img {
  width: 100%;
  height: auto; }

